* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.header {
  background-color: black;
  color: white;

  .header_box {
    width: 1245px;
    height: 125px;
    margin: 0 auto;

    .gnb_outer {
      position: relative;
      height: 75px;
      font-size: 14px;

      .logo {
        width: 174px;
        height: 47px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .logo img {
          width: 100%;
          height: 100%;
        }
      }

      .gnb {
        display: flex;
        height: 100%;
        justify-content: flex-end;
        align-items: center;

        ul {
          li {
            display: inline;
            margin-right: 17px;

            a {
              color: white;
            }
          }
        }
      }
    } //gnb_outer

    .lnb_outer {
      display: flex;
      font-size: 24px;
      justify-content: space-between;
      align-items: center;
      font-size: 24px;
      height: 50px;
      .lnb {
        ul {
          li {
            display: inline;
            justify-content: space-between;
            margin-right: 15px;
            font-weight: bold;
            position: relative;
            :hover .sub_menu {
              height: 300px;
            }
            .sub_menu {
              top: 100%;
              left: 50%;
              transform: translateX(-50%);
              width: 100%;
              position: absolute;
              overflow: hidden;
              width: 1245px;
              li {
                font-size: 12px;
                font-weight: 100;
                top: 50px;
                display: inline-block;
                a {
                }
              }
            }

            a {
              color: white;
            }
          }
        }
      }
      .lnb_rank {
        font-size: 14px;
      }
    } //lnb_outer
  } //header_box
} //header

.wrap_ex {
  width: 1245px;
  margin: 0 auto;
  padding-top: 90px;

  .pr_wrap {
    display: flex;
    .pr_visual {
      margin-right: 215px;
      width: 510px;
      .pr_scode {
        display: flex;
        p {
          margin-right: 15px;
          color: #646464;
        }
        span {
          font-weight: bold;
          color: #201f1f;
        }
      }

      .pr_img {
        margin-top: 15px;
        width: 510px;
        height: 540px;
        background-color: #e5e5e5;
        border: 3px solid #d9d9d9;
        img {
          width: 100%;
          height: 100%;
        }
      }

      .pr_hoverimg {
        margin: 30px 0;
        ul {
          display: flex;
          justify-content: space-between;
          li {
            width: 90px;
            height: 90px;
            line-height: 90px;
            background-color: #e5e5e5;
            border: 3px solid #d9d9d9;
            text-align: center;

            :last-child {
            }
            a {
              color: black;
            }
          }
        }
      }

      .wear_product {
        margin-top: 45px;
        margin-bottom: 25px;
        .wp_title {
          font-size: 20px;
          margin: 10px 0;
        }
        .wp_img {
          width: 195px;
          height: 195px;
          line-height: 195px;
          text-align: center;
          background-color: #e5e5e5;
          border: 3px solid #d9d9d9;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .wp_name {
          font-size: 11px;
        }

        .wp_color {
          font-size: 11px;
        }

        .wp_price {
          font-size: 11px;
          font-weight: bold;
        }
      } //wear_product

      .recently_product {
        display: flex;
        .rv {
          margin-right: 15px;
          .rv_title {
            font-size: 20px;
            margin: 10px 0;
          }
          .rv_img {
            width: 195px;
            height: 195px;
            line-height: 195px;
            text-align: center;
            background-color: #e5e5e5;
            border: 3px solid #d9d9d9;
            img {
              width: 100%;
              height: 100%;
            }
          }

          .rv_color {
            font-size: 11px;
          }
          .rv_name {
            font-size: 11px;
          }
          .rv_price {
            font-size: 11px;
            font-weight: bold;
          }
        }
      }
    } //pr_visual

    .pr_option {
      width: 405px;
      .text_box {
        border-bottom: 3px solid #e8e8e8;
        .pr_name {
          margin: 25px 0;
          font-size: 30px;
          font-weight: bold;
        }

        .pr_info {
          width: 40px;
          height: 22px;
          font-size: 13px;
          text-align: center;
          color: white;
        }

        .pr_best {
          background-color: #545adc;
        }

        .pr_sns {
          display: flex;
          justify-content: space-between;
          margin: 30px 0;
          .rating {
            display: flex;
            :last-child {
              margin-right: 5px;
            }
          }
          .sns {
            font-size: 22px;
            display: flex;
            .save {
              padding: 0 18px;
              color: red;
            }
            .facebook {
              padding: 0 23px;
            }
            .twitter {
              padding-right: 15px;
            }
          }
        }

        .pr_price {
          .price_orgin {
            font-size: 20px;
            font-weight: bold;
            p {
            }
          }

          .accumulate {
            display: flex;
            margin-top: 13px;
            margin-bottom: 20px;
          }
        } //pr_price

        .delivery_info {
          border-top: 3px solid #e8e8e8;
          display: flex;
          flex-direction: column;
          .info_box {
            font-size: 13px;
            display: flex;
            padding-top: 20px;
            padding-bottom: 20px;
            display: flex;
            justify-content: space-between;
            .info_title {
            }

            .info_txt {
              a {
                i {
                  color: #bdb6b6;
                }
              }
              p {
                span {
                  font-weight: bold;
                  color: #4136bf;
                  padding-right: 5px;
                }
                :nth-child(2) {
                  margin-top: 10px;
                }
              }
            }
          }

          .credit_box {
            font-size: 13px;
            display: flex;
            justify-content: space-between;
            padding-bottom: 25px;
            .credit_title {
            }
            .credit_txt {
              p {
                span {
                  color: #b0b0b0;
                }
              }
            }
          }
        }
      } //text_box

      .pr_color {
        padding: 20px 0;
        .color_title {
          font-size: 15px;
          color: #b0b0b0;
        }
        .color_wrapper {
          display: flex;
          .color {
            margin-right: 15px;
            input {
              display: none;
            }
            label {
              margin: 10px 0;
              display: block;
              width: 90px;
              height: 90px;
              background-color: #e5e5e5;
              border: 3px solid #d9d9d9;
              img {
              }
            }
          }
        } //color_wrapper
      } //pr_color

      .pr_size {
        .size_title {
          font-size: 15px;
          color: #b0b0b0;
        }
        .size_wrapper {
          display: flex;
          justify-content: space-around;
          .size {
            margin: 10px 0;
            .size_btn {
              text-align: center;
              border-radius: 10px 10px;
              width: 72px;
              height: 57px;
              background-color: #d9d9d9;
            }
          }
        }
      } //pr_size

      .n_pay {
        display: flex;
        padding: 20px 0;
        justify-content: space-between;
        .n_pay_left_area {
          display: flex;
          .n_pay_img {
            width: 63px;
            margin-right: 5px;
            img {
              width: 100%;
            }
          } //n_pay_img
        } //n_pay_left_area

        span {
          color: #2db400;
        }
      } //n_pay

      .pr_buy {
        .size_box {
          margin-bottom: 10px;
          .size_btn {
            text-align: center;
            width: 405px;
            height: 60px;
            font-size: 15px;
            color: #000;
            border: 1px solid #bbbbbb;
            font-weight: bold;
          }

          i {
            font-size: 22px;
          }
        }

        .btn_box {
          display: flex;
          justify-content: space-between;
          .cart_box {
            .cart_btn {
              width: 128px;
              height: 60px;
              text-align: center;
              border: 2px solid #000;
            }
          }
          .buy_box {
            .buy_btn {
              width: 267px;
              height: 60px;
              text-align: center;
              background-color: #4136bf;
              color: white;
            }
          }
        }

        .p_txt {
          font-size: 12px;
          margin-top: 10px;
        }
      }
    } //pr_option

    .detail_tab_menu {
    } //detaill_tab_menu
  } //pr_wrap

  .tab_area {
    margin-top: 30px;
    .tab-buttons {
      font-size: 0;
      line-height: 0;
      margin-bottom: 20px;
      :hover {
        background-color: #ffbf00;
      }
      button {
        margin: 0;
        padding: 0;
        width: calc(1245px / 5);
        height: 50px;
        background-color: #d9d9d9;
        border: 1px solid #bbbbbb;
        font-size: 20px;
      }
    }
    .tab {
      // height: 930px;
    }

    #tab1 {
      height: 205px;
      padding: 30px 0;

      h2 {
        font-size: 17px;
        text-decoration: underline;
        margin-bottom: 10px;
        i {
          text-decoration: none;
        }
      }
      p {
      }
    }
    #tab2 {
      margin: 60px 0;
      .review_title {
        margin-left: 30px;
        display: flex;
        justify-content: space-between;
        margin-bottom: 25px;
        p {
          font-size: 20px;
        }
        button {
          background-color: #4136bf;
          color: white;
          padding: 5px 15px;
        }
      }
      .review_grid {
        .review {
          :first-child {
          }
          padding: 47px 0;
          border-bottom: 1px solid black;
          display: flex;
          justify-content: space-around;
          .review_text {
            width: 720px;
            font-size: 20px;
          }
          .review_rating {
            width: 90px;
            display: flex;
          }
          .review_date {
            width: 90px;
          }
          .review_user {
            width: 90px;
          }
        }

        .border-top {
          border-top: 3px solid black;
        }
        .border-bottom {
          border-bottom: 3px solid black;
        }
      }
    }
    #tab3 {
      margin: 80px 0;
      .inquiry_text {
        font-size: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        p {
          margin-top: 5px;
          span {
            color: #545adc;
            font-weight: bold;
          }
        }
        button {
          margin-top: 20px;
          border: 3px solid #545adc;
          color: #545adc;
          width: 195px;
          height: 60px;
          font-size: 20px;
          font-weight: bold;
        }
      }

      .inquiry_title {
        font-size: 20px;
        margin-left: 35px;
        border-bottom: 3px solid black;
        padding-bottom: 20px;
      }

      .inquiry {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        margin-left: 20px;
        border-bottom: 1px solid black;

        .inquiry_outer {
          padding: 35px 0;
          display: flex;
          justify-content: space-evenly;
          border-bottom: 1px solid black;
          .inquiry_state {
            width: 90px;
            color: gray;
            font-weight: bold;
          }
          .inquiry_txt {
            width: 825px;
            color: gray;
            font-weight: bold;
            p {
              i {
                color: #545adc;
              }
            }
          }

          .inquiry_date {
            width: 90px;
          }

          .inquiry_user {
            width: 90px;
          }

          .inquiry_btn {
          }
        }

        .respond_box {
          display: flex;
          padding: 35px 0;
          .respond_title {
            width: 90px;
            margin-right: 15px;
            margin-left: 20px;
            font-size: 15px;
            p {
            }
          }

          .respond_txt {
            font-size: 13px;
            p {
              span {
                color: gray;
                font-weight: bold;
              }
            }
          }
          display: none;
        }
      }
    }
    #tab4 {
      margin: 30px 0;
      h2 {
        font-size: 17px;
        text-decoration: underline;
        margin-bottom: 10px;
        i {
          text-decoration: none;
        }
      }
      p {
      }
    }
    #tab5 {
      height: 455px;
      .tab_info {
        h2 {
          font-size: 17px;
          margin-bottom: 10px;
          text-decoration: underline;
          i {
            text-decoration: none;
          }
        }
        p {
          a {
            color: gray;
            font-weight: bold;
          }
        }
        margin: 50px 0;
      }
    }
  } //tab_area
} //wrap_ex

.top_btn {
  background-color: rgba(0, 0, 0, 0.3);
  width: 75px;
  height: 75px;
  color: white;
  text-align: center;
  line-height: 75px;
  cursor: pointer;
  position: fixed;
  bottom: 100px;
  right: 30px;
  opacity: 0;
  border-radius: 50%;
}
//TOP_btn

.footer {
  .footer_outer {
    background-color: black;
    .footer_link {
      margin: 0 auto;
      display: flex;
      justify-content: center;
      background-color: white;
      .fnb {
        margin: 28px 0;

        li {
          display: inline;
          padding: 0 60px;
          font-weight: bold;

          a {
            color: black;
          }
        }
      }
    } //footer_link

    .footer_inner {
      width: 1245px;
      margin: 0 auto;
      color: black;
      .top_area {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .fnb {
          padding-top: 8px;
          ul {
            li {
              display: inline;
              padding-right: 30px;
              font-size: 12px;
              :first-child {
                padding-left: 10px;
              }
              a {
                color: white;
              }
            }
          }
        }

        .sns {
          ul {
            li {
              display: inline;
              padding: 0 15px;
              a {
                font-size: 45px;
                color: white;
              }
            }
          }
        }
      } //top_area

      .mid_area {
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
        padding: 0 8px;
        height: 190px;
        .copyright {
          p {
            font-size: 14px;
            margin-bottom: 18px;
            color: white;
          }
        }
        .address {
          width: 376px;
          .detail {
            color: white;
            span {
              float: left;
              margin-right: 120px;
              font-weight: bold;
              margin-top: 13px;
            }
            p {
              float: right;
              margin-top: 13px;
            }
          }
        }
      } //mid_area

      .bottom_area {
        padding: 0 8px;
        margin-top: 45px;
        padding-bottom: 30px;
        .footer_menu {
          height: 30px;
          line-height: 30px;
          ul {
            li {
              font-size: 14px;
              display: inline;
              padding-right: 60px;
              a {
                color: white;
              }
            }
          }
        }
      } //bottom_area
    } //footer_inner
  } //footer_outer
} //footer
